import "react";

import Meter from "./Meter";

const Guesses = ({ validCommonWords, correctGuesses }) => {
  const wordsByLength = {};
  validCommonWords.forEach((word) => {
    if (!(word.length in wordsByLength))
      wordsByLength[word.length] = {
        guessed: [],
        remaining: 0,
      };
    if (correctGuesses.includes(word)) {
      wordsByLength[word.length].guessed.push(word);
    } else {
      wordsByLength[word.length].remaining++;
    }
  });
  const wordsByLengthArray = Object.keys(wordsByLength)
    .map(Number)
    .toSorted((a, b) => a - b)
    .map((k) => ({
      length: k,
      remaining: wordsByLength[k].remaining,
      guessed: wordsByLength[k].guessed.toSorted(),
    }));
  return (
    <div className="guesses-wrapper">
      <Meter
        max={validCommonWords.length}
        value={correctGuesses.length}
        height="100px"
        text={`${correctGuesses.length} / ${validCommonWords.length}`}
      />
      <div className="sub-guesses-wrapper">
        {wordsByLengthArray.map(({ length, remaining, guessed }) => (
          <div className="sub-guess" key={length}>
            <div className="sub-guess-length">{length} characters</div>
            <div className="sub-guess-guesses">{guessed.join(", ")}</div>
            <div className="sub-guess-remaining">{remaining} left</div>
            <Meter max={guessed.length + remaining} value={guessed.length} />
          </div>
        ))}
      </div>
    </div>
  );
};

export default Guesses;
