import "./App.scss";

import "react";

const Answers = ({ words }) => {
  const wordsByLength = {};
  words.forEach((word) => {
    if (!(word.length in wordsByLength)) wordsByLength[word.length] = [];
    wordsByLength[word.length].push(word);
  });
  const wordsByLengthArray = Object.keys(wordsByLength)
    .map(Number)
    .toSorted((a, b) => a - b)
    .map((k) => [k, wordsByLength[k]]);
  return (
    <div>
      <p>{words.length} words in total</p>
      {wordsByLengthArray.map(([length, wordsOfLength]) => (
        <p key={length}>
          {length}: {wordsOfLength.join(" • ")}
        </p>
      ))}
    </div>
  );
};

export default Answers;
