import "./App.scss";

import { useState } from "react";

const isValidMove = (oldKey, newKey) => {
  const [a, b] = [oldKey, newKey].map((key) => [Math.floor(key / 4), key % 4]);
  const [ax, ay] = a;
  const [bx, by] = b;
  if (ax === bx && ay === by) return false;
  return Math.abs(ax - bx) < 2 && Math.abs(ay - by) < 2;
};

const Grid = ({ grid, makeGuess, wordsPerGridSquare, correctGuesses }) => {
  const [path, setPath] = useState([]);
  const [nthOccurrence, setNthOccurrence] = useState(0);
  const currentGuess = path
    .map((key) => grid[Math.floor(key / 4)][key % 4])
    .join("");
  const handleTileClick = (key) => {
    if (path.includes(key)) {
      setPath(path.slice(0, path.indexOf(key)));
      return;
    }
    if (path.length === 0 || isValidMove(path[path.length - 1], key)) {
      setPath([...path, key]);
      return;
    }
    setPath([]);
  };
  const handleGuessClick = () => {
    makeGuess(currentGuess);
    setPath([]);
  };
  const selectNthOccurrence = (thisPath, char, n) => {
    const occurrences = [];
    for (let k = 0; k < 16; k++) {
      if (grid[Math.floor(k / 4)][k % 4] === char && !thisPath.includes(k)) {
        if (
          thisPath.length === 0 ||
          isValidMove(thisPath[thisPath.length - 1], k)
        ) {
          occurrences.push(k);
        }
      }
    }
    if (occurrences.length === 0) return;
    const newKey = occurrences[n % occurrences.length];
    setPath([...path, newKey]);
  };
  const handleKeyDown = (event) => {
    const char = event.key.toLowerCase();
    if (char === "backspace") {
      setPath(path.slice(0, path.length - 1));
      return;
    } else if (char === " ") {
      event.preventDefault();
      const oldK = path[path.length - 1];
      selectNthOccurrence(
        path.slice(0, path.length - 1),
        grid[Math.floor(oldK / 4)][oldK % 4],
        nthOccurrence + 1
      );
      setNthOccurrence(nthOccurrence + 1);
    } else if (char === ".") {
      handleGuessClick();
    } else {
      selectNthOccurrence(path, char, 0);
      setNthOccurrence(0);
    }
  };
  return (
    <>
      <div className="grid" onKeyDown={handleKeyDown}>
        {grid.map((row, i) =>
          row.map((c, j) => {
            const key = 4 * i + j;
            let classes = ["grid-tile", "tile-button"];
            if (path.includes(key)) classes.push("selected");
            const unguessedWords =
              wordsPerGridSquare[key]?.wordsStartingHere?.filter(
                (word) => !correctGuesses.includes(word)
              ) ?? [];
            const wordsPassingHere = Array.from(
              wordsPerGridSquare[key]?.wordsPassingHere ?? []
            );
            const isSpent = wordsPassingHere.every((word) =>
              correctGuesses.includes(word)
            );
            if (isSpent) classes.push("spent");
            return (
              <button
                type="button"
                className={classes.join(" ")}
                key={key}
                onClick={() => handleTileClick(key)}
              >
                <span className="grid-tile-contents">{c}</span>
                <div className="grid-tile-count">
                  {unguessedWords.length || ""}
                </div>
              </button>
            );
          })
        )}
      </div>
      <button
        type="button"
        className="active-guess tile-button"
        onClick={handleGuessClick}
      >
        {currentGuess || "?"}
      </button>
    </>
  );
};

export default Grid;
