import "react";

const Meter = ({ max, value, text, height = "10px" }) => {
  const pct = ((100 * value) / max).toFixed(1);
  const primaryColor = `hsl(${pct}deg 100% 50%)`;
  return (
    <div className="meter-container" style={{ height }}>
      <div
        className="meter-bar"
        style={{ background: primaryColor, right: `${100 - pct}%` }}
      />
      <div className="meter-text" style={{ lineHeight: height }}>
        {text}
      </div>
    </div>
  );
};

export default Meter;
