import {
  ALLOWED_CHARS,
  DELTAS,
  GENERATION_CYCLES,
  LETTERS_BY_FREQUENCY,
  LETTERS_BY_FREQUENCY_TOTAL,
  MIN_WORD_LENGTH,
} from "./constants";

const MAX_WORD_LENGTH = 16;

export const getWordListFromRawText = (rawText) => {
  const allowedCharsSet = new Set(ALLOWED_CHARS.split(""));
  return rawText
    .split(`\n`)
    .filter((w) => w.length >= MIN_WORD_LENGTH && w.length <= MAX_WORD_LENGTH)
    .filter((w) => w.split("").every((c) => allowedCharsSet.has(c)));
};

export const getRandomGrid = () => {
  const randomChars = new Array(4).fill(0);
  return randomChars.map(() => {
    const arr = new Array(4).fill(0);
    return arr.map(() => {
      const randomPct = Math.random() * LETTERS_BY_FREQUENCY_TOTAL;
      let total = 0;
      for (let i = 0; i < LETTERS_BY_FREQUENCY.length; i++) {
        const [c, p] = LETTERS_BY_FREQUENCY[i];
        if (total <= randomPct && total + p > randomPct) return c.toLowerCase();
        total += p;
      }
      return "z";
    });
  });
};

export const isValid = (chars, used, x, y, word, grid) => {
  if (chars.length === 0) {
    return true;
  }
  const allMoves = DELTAS.map(([dx, dy]) => [x + dx, y + dy]);
  const inBoundsMoves = allMoves.filter((coords) =>
    coords.every((coord) => coord >= 0 && coord < 4)
  );
  const unusedMoves = inBoundsMoves.filter(
    ([nx, ny]) => !used.has(4 * ny + nx)
  );
  const validMoves = unusedMoves.filter(
    ([nx, ny]) => grid[ny][nx] === chars[0]
  );

  const newChars = chars.slice(1);
  return validMoves.some(([nx, ny]) => {
    return isValid(
      newChars,
      new Set([4 * ny + nx]).union(used),
      nx,
      ny,
      word,
      grid
    );
  });
};

const getTilesForAllValidPaths = (chars, used, x, y, word, grid) => {
  if (chars.length === 0) {
    return used;
  }
  const allMoves = DELTAS.map(([dx, dy]) => [x + dx, y + dy]);
  const inBoundsMoves = allMoves.filter((coords) =>
    coords.every((coord) => coord >= 0 && coord < 4)
  );
  const unusedMoves = inBoundsMoves.filter(
    ([nx, ny]) => !used.has(4 * ny + nx)
  );
  const validMoves = unusedMoves.filter(
    ([nx, ny]) => grid[ny][nx] === chars[0]
  );

  const newChars = chars.slice(1);
  let union = new Set();
  validMoves.forEach(([nx, ny]) => {
    union = union.union(
      getTilesForAllValidPaths(
        newChars,
        new Set([4 * ny + nx]).union(used),
        nx,
        ny,
        word,
        grid
      )
    );
  });
  return union;
};

export const getWordsValidForGrid = (words, grid) => {
  return words.filter((word) => {
    const chars = word.split("");
    return grid.some((row, y) => {
      return row.some((c, x) => {
        return (
          c === chars[0] &&
          isValid(chars.slice(1), new Set([4 * y + x]), x, y, word, grid)
        );
      });
    });
  });
};

export const getNewGridAndValidWords = (wordsList, wantFewerWords) => {
  let [bestGrid, bestValidWordsForGrid, bestLongestWordLength] = [null, [], 0];
  for (let i = 0; i < GENERATION_CYCLES; i++) {
    const xmasGrid = `
    INOD
    LL'O
    LTNA
    ENIC`
      .split("\n")
      .map((x) => x.trim())
      .filter(Boolean)
      .map((x) => x.toLowerCase().split(""));
    const customGrid = `
    XXXX
    I'D'
    XXXV
    XTSE`
      .split("\n")
      .map((x) => x.trim())
      .filter(Boolean)
      .map((x) => x.toLowerCase().split(""));
    const newGrid = xmasGrid; //getRandomGrid();
    // const newGrid = customGrid; //getRandomGrid();
    // const newGrid = getRandomGrid();
    const validWordsForGrid = getWordsValidForGrid(wordsList, newGrid);
    const longestWordLength = Math.max(
      ...validWordsForGrid.map((x) => x.length)
    );
    const wordCountIsBetter = wantFewerWords
      ? validWordsForGrid.length < bestValidWordsForGrid.length
      : validWordsForGrid.length > bestValidWordsForGrid.length;
    if (
      longestWordLength > bestLongestWordLength ||
      (longestWordLength === bestLongestWordLength && wordCountIsBetter)
    ) {
      bestGrid = newGrid;
      bestValidWordsForGrid = validWordsForGrid;
      bestLongestWordLength = longestWordLength;
    }
  }
  return [bestGrid, bestValidWordsForGrid];
};

export const getWordsPerGridSquare = (validWords, grid) => {
  const wordsPerGridSquare = [];
  validWords.forEach((word) => {
    const chars = word.split("");
    grid.forEach((row, y) => {
      row.forEach((c, x) => {
        if (
          c === chars[0] &&
          isValid(chars.slice(1), new Set([4 * y + x]), x, y, word, grid)
        ) {
          const key = 4 * y + x;
          if (!(key in wordsPerGridSquare))
            wordsPerGridSquare[key] = {
              wordsStartingHere: [],
              wordsPassingHere: new Set(),
            };
          wordsPerGridSquare[key].wordsStartingHere.push(word);
          const tiles = Array.from(
            getTilesForAllValidPaths(
              chars.slice(1),
              new Set([4 * y + x]),
              x,
              y,
              word,
              grid
            )
          );
          tiles.forEach((tileKey) => {
            if (!(tileKey in wordsPerGridSquare))
              wordsPerGridSquare[tileKey] = {
                wordsStartingHere: [],
                wordsPassingHere: new Set(),
              };
            wordsPerGridSquare[tileKey].wordsPassingHere.add(word);
          });
        }
      });
    });
  });
  return wordsPerGridSquare;
};
